

import 'reflect-metadata'
// 1. 对象上定义元数据
const obj = {
  username: 'admin',
  age: 18,
  info() {
    console.log('info')
  }
}
Reflect.defineMetadata('data', 'test', obj)
const value = Reflect.getMetadata('data', obj)
console.log(value) // test

// 2. 对象的属性上定义元数据
Reflect.defineMetadata('data2', 'test2', obj, 'username')
const value2 = Reflect.getMetadata('data2', obj, 'username')
console.log(value2) // test2
console.log(Reflect.getMetadata('data2', obj, 'age')) // undefined
console.log(Reflect.getMetadata('data2', obj)) // undefined
// Reflect.hasMetadata
console.log(Reflect.hasMetadata('data2', obj, 'username')) // true
console.log(Reflect.hasMetadata('data2', obj, 'age')) // false

// 3. 在类上定义元数据
@Reflect.metadata('des', '都是人类')
class People {
  @Reflect.metadata('test', '姓名不能包含非法字符')
  username = '王五'
  @Reflect.metadata('importInfo', '去陶然居吃饭')
  eat() {
    console.log('eat')
  }
}
console.log(Reflect.getMetadata('des', People)) // 都是人类
console.log(Reflect.getMetadata('test', new People(), 'username')) // 姓名不能包含非法字符
console.log(Reflect.getMetadata('test', People.prototype, 'username')) // 姓名不能包含非法字符
console.log(Reflect.getMetadata('importInfo', new People(), 'eat')) // 去陶然居吃饭
console.log(Reflect.getMetadata('importInfo', People.prototype, 'eat')) // 去陶然居吃饭
console.log(Reflect.hasMetadata('importInfo', People.prototype, 'eat')) // true

// 4. 元数据的继承
class ChinesePeople extends People {
  @Reflect.metadata('des', '中国人')
  birthday: string = '1990-01-01'
  @Reflect.metadata('firstName', '姓')
  @Reflect.metadata('lastName', '名')
  getFullName(name: string, age: number): number {
    return 100
  }
}
console.log(Reflect.getMetadata('des', ChinesePeople)) // 都是人类
console.log(Reflect.hasMetadata('des', ChinesePeople)) // true
console.log(Reflect.hasOwnMetadata('des', ChinesePeople)) // false
console.log(Reflect.hasMetadata('importInfo', ChinesePeople.prototype, 'eat')) // true
console.log(Reflect.hasOwnMetadata('importInfo', ChinesePeople.prototype, 'eat')) // false

// 5. Reflect.getMetadataKeys
const keys = Reflect.getMetadataKeys(ChinesePeople.prototype, 'getFullName') // ['design:returntype', 'design:paramtypes', 'design:type', 'lastName', 'firstName']
keys.forEach(key => {
  console.log(key, ':', Reflect.getMetadata(key, ChinesePeople.prototype, 'getFullName'))
})
/* 打印输出：
design:returntype : f Number() // 被修饰的方法getFullName的返回值类型为number，在js看来，number类型就是一个Number函数
design:paramtypes : [f String(), f Number()] // 被修饰的方法getFullName的参数类型为String和Number
design:type : f Function() // 被修饰的方法getFullName的类型为Function
lastName : 名
firstName : 姓
前三个是这个包内置的，
*/
const keys2 = Reflect.getMetadataKeys(ChinesePeople.prototype, 'birthday')
keys2.forEach(key => {
  console.log(key, ':', Reflect.getMetadata(key, ChinesePeople.prototype, 'birthday'))
})
/* 打印输出：
design:type : ƒ String()
des : 中国人
*/

// 6. Reflect.getOwnMetadataKeys只获取自身的元数据，Reflect.getMetadataKeys获取自身和父类的元数据key
console.log(Reflect.getMetadataKeys(ChinesePeople.prototype, 'eat')) // ['design:returntype', 'design:paramtypes', 'design:type', 'importInfo']
console.log(Reflect.getOwnMetadataKeys(ChinesePeople.prototype, 'eat')) // []

// 以上为基础知识过渡，真正使用元数据的场景是在装饰器内部定义和获取，而不是在外面控制器通过@Reflect.metadata()定义